<template>
    <div>
        <Card shadow style="margin: 8px;width: 252px; float: left;" v-for="(item, idx) in albums" :key="item.id">
            <h4 slot="title">{{item.name}}</h4>
            <a href="#" slot="extra">
                <Icon type="ios-loop-strong"></Icon>
                <Icon type="ios-star" v-for="n in idx%4" :key="n"></Icon>
            </a>
            <div style="text-align:center">
                <img :src="item.cover" width="200px" height="260px" @click="handleSelect(item.id)" />
            </div>
            <div style="text-align:center">
                <span><Icon type="ios-hand" size="20"/>: {{item.click_num}}</span> ·
                <span><Icon type="ios-eye" size="24"/>: {{item.favor_num}} </span> ·
                <span><Icon type="ios-heart" size="20"/>: 100 </span>
            </div>
        </Card>
    </div>
</template>

<script>
    export default {
        name: 'album-list',
        props: {'albums': Array},
        data() {
            return {
                count: 0
            }
        },
        methods: {
            handleSelect(id) {
                console.log(id);
                this.$router.push({path: `/album/` + id});
            }
        },
        created() {
        }
    }
</script>

