<template>
    <main-container :is-collpased="isCollapsed">
        <template slot="sidebar">
            <Menu :active-name="activeCat2Name" theme="dark" width="auto" class="sider-menu" :class="menuitemClasses"
                  @on-select="handleSidebarSelect">
                <MenuItem v-for="item in leftSides" :key="item.id" :name="item.name">
                    <Icon type="ios-navigate"></Icon>
                    <span>{{item.name}}</span>
                </MenuItem>
            </Menu>
        </template>
        <template slot="content">
            <Card>
                <Row :gutter="8">
                    <i-col :span="16">
                        <Row>
                            <i-col :span="4">
                                <Button @click="handleOrdering('click_num')">
                                    <Icon type="ios-hand" size="20"/>
                                    点击量
                                    <Icon v-if="orderingName === 'click_num' & orderingType === ''"
                                          type="ios-arrow-round-up" :size="24"/>
                                    <Icon v-if="orderingName === 'click_num' & orderingType === '-'"
                                          type="ios-arrow-round-down" :size="24"/>
                                </Button>
                            </i-col>
                            <i-col :span="4">
                                <Button @click="handleOrdering('focus_num')">
                                    <Icon type="ios-eye" size="24"/>
                                    关注量
                                    <Icon v-if="orderingName === 'focus_num' & orderingType === ''"
                                          type="ios-arrow-round-up" :size="24"/>
                                    <Icon v-if="orderingName === 'focus_num' & orderingType === '-'"
                                          type="ios-arrow-round-down" :size="24"/>
                                </Button>
                            </i-col>
                            <i-col :span="4">
                                <Button>
                                    <Icon type="ios-love" size="20"/>
                                    <Icon type="ios-heart" size="20"/>
                                    点赞量
                                    <Icon type="ios-arrow-round-up" :size="24"/>
                                </Button>
                            </i-col>
                            <i-col :span="4">
                                <Button @click="handleOrdering('add_time')">
                                    <Icon type="ios-clock" size="20"/>
                                    发布时间
                                    <Icon v-if="orderingName === 'add_time' & orderingType === ''"
                                          type="ios-arrow-round-up" :size="24"/>
                                    <Icon v-if="orderingName === 'add_time' & orderingType === '-'"
                                          type="ios-arrow-round-down" :size="24"/>
                                </Button>
                            </i-col>
                            <i-col :span="3">
                                <Button @click="moterModal=true">热门模特儿</Button>
                            </i-col>
                            <i-col :span="5">
                                <!--<Button @click="pubModal=true">所有出品方</Button>-->
                                <Select v-model="publisher" clearable placeholder="请选择摄影出品机构"
                                        @on-change="selectByPublisher">
                                    <Option v-for="pub in publishers" :value="pub.id" :key="pub.id">{{ pub.name }}
                                    </Option>
                                </Select>
                            </i-col>
                            <Modal
                                    v-model="moterModal"
                                    title="请选择一个模特儿" width="50">
                                <Row>
                                    <h4>下面列出了一些热门模特的名字，您还可以在搜索框中输入模特儿的名字或名字的一部分进行搜索，
                                        比如输入：'纯' 或 '王雨纯' 或 '王语纯'！
                                    </h4>
                                </Row>
                                <Divider>最火模特儿</Divider>
                                <Row>
                                    <Button v-for="moter in moters" class="btn-moter" @click="selectByMoter(moter.id)">
                                        {{moter.name}}
                                    </Button>
                                </Row>
                            </Modal>
                        </Row>
                    </i-col>
                    <i-col :span="8">
                        <Row>
                            <Input v-model="search" search enter-button="搜索" placeholder="根据关键字搜索专辑..."
                                   @click="handleSearch"/>
                        </Row>
                    </i-col>
                </Row>
                <Row>
                    <Page :total="albumCountTotal" :page-size="albumCountPerPage" prev-text="上一页" next-text="下一页"
                          @on-change="hanglePaginate" :current="currentPage"/>
                </Row>
            </Card>
            <Row>
                <album-list :albums="albums"></album-list>
            </Row>
            <Row>
                <Page :total="albumCountTotal" :page-size="albumCountPerPage" prev-text="上一页" next-text="下一页"
                      @on-change="hanglePaginate" :current="currentPage"/>
            </Row>
        </template>
    </main-container>
</template>

<script>
    import {getAlbums, getMoters, getPublishers} from "../../api/api";
    import AlbumList from '../../components/AlbumList'

    export default {
        name: "zgmn",
        components: {AlbumList},
        data() {
            return {
                isCollapsed: false,
                leftSides: [],
                activeCat2Name: -1,
                publishers: [],
                publisher: '',
                moters: [],
                moter: '',
                albums: [],
                albumCountTotal: 0,
                albumCountPerPage: 12,
                currentPage: 1,
                orderingName: 'click_num',
                orderingType: '-',
                search: '',
                pubModal: false,
                moterModal: false,
            }
        },
        computed: {
            menuitemClasses: function () {
                return [
                    'menu-item', this.isCollapsed ? 'collapsed-menu' : ''
                ]
            },
            ordering: function () {
                return this.orderingType + this.orderingName
            }
        },
        watch: {
            orderingType: function (val) {
                let ordering = val + this.orderingName;
                this.obtainAlbums({
                    category: 1,
                    ordering: ordering,
                    publisher: this.publisher,
                    search: this.search,
                    page: this.currentPage
                })
            },
            search: function (val) {
                this.obtainAlbums({
                    category: 1,
                    ordering: this.ordering,
                    publisher: this.publisher,
                    search: val,
                    page: this.currentPage
                })
            }
        },
        methods: {
            handleSidebarSelect() {
                console.log('哈哈哈哈哈哈哈哈哈哈或或或')
            },
            obtainPublishers() {
                getPublishers({}).then(
                    (response) => {
                        this.publishers = response.data
                    }
                )
            },
            obtainMoters() {
                getMoters({params: {hotness_min: 1}}).then(
                    (response) => {
                        this.moters = response.data
                    }
                )
            },
            handleSearch() {
                this.obtainAlbums({category: 1, ordering: this.ordering, search: this.search, page: this.currentPage})
            },
            handleOrdering(orderName) {
                this.orderingName = orderName
                if (this.orderingType === '') {
                    this.orderingType = '-'
                } else {
                    this.orderingType = ''
                }
                console.log(this.ordering)
            },
            obtainAlbums(queryParams) {
                getAlbums({params: queryParams}).then(
                    (response) => {
                        this.albums = response.data.results;
                        this.albumCountTotal = response.data.count;
                    }
                )
            },
            hanglePaginate(current) {
                this.currentPage = current;
                this.currentPage = current;
                const msg = this.$Message.loading({
                    content: '正在加载中...',
                    duration: 0
                });
                setTimeout(msg, 1000);
                this.obtainAlbums({
                    category: 1,
                    ordering: this.ordering,
                    publisher: this.publisher,
                    moter: this.moter,
                    page: this.currentPage
                });
            },
            selectByMoter(moterId) {
                this.moter = moterId;
                this.obtainAlbums({ordering: this.ordering, moter: this.moter, page: this.currentPage});
            },
            selectByPublisher() {
                this.obtainAlbums({ordering: this.ordering, publisher: this.publisher, page: this.currentPage});
            },
        },
        created() {
            this.obtainAlbums({
                category: 1,
                ordering: this.ordering,
                publisher: this.publisher,
                moter: this.moter,
                page: this.currentPage
            });
            this.obtainPublishers();
            this.obtainMoters();
        }
    }
</script>

<style scoped>
    .sider-menu {
        margin-top: 60px;
    }

    .menu-item span {
        display: inline-block;
        overflow: hidden;
        width: 69px;
        text-overflow: ellipsis;
        white-space: nowrap;
        vertical-align: bottom;
        transition: width .2s ease .2s;
    }

    .menu-item i {
        transform: translateX(0px);
        transition: font-size .2s ease, transform .2s ease;
        vertical-align: middle;
        font-size: 16px;
    }

    .collapsed-menu span {
        /*width: 0;*/
        transition: width .2s ease;
    }

    .collapsed-menu i {
        transform: translateX(5px);
        transition: font-size .2s ease .2s, transform .2s ease .2s;
        vertical-align: middle;
        font-size: 22px;
    }

    .btn-pub {
        margin-bottom: 16px;
        margin-right: 16px;
    }

    .btn-moter {
        margin-bottom: 8px;
        margin-right: 8px;
    }
</style>